import styled from 'styled-components';


export const ToNightWrapper = styled.div`
    overflow:hidden;
    margin-top:20px;
`

export const ToNightLeft = styled.div`
    float:left;
    width:570px;
`

export const ListWrapper = styled.div`

`
export const Item = styled.div`
    position: relative;
    margin-bottom:20px;
    padding:10px;
    font-size: 14px;
    color:#503e2c;
    background:#fff;
    box-shadow: 0 2px 3px rgba(0,0,0,.1);
    .content{
        padding: 5px 0 10px;
        word-wrap: break-word;
        word-break: break-all;
        line-height:1.5;
        border-bottom: 1px solid #efefef;
    }
    .author{
        text-align:right;
        padding: 10px 0 5px;
        color:#999;
        font-size: smaller;
        .name{
            display:inline-block;
            padding-right:10px;
        }
        .time{
            display:inline-block;
        }
    }
`

export const ToNightRight = styled.div`
    float:right;
    width:260px;
`

export const AboutWrapper = styled.div`
    margin-bottom: 20px;
    background: #fff;
    padding: 20px;
`
export const AboutTitle = styled.div`
    text-align:center;
    font-size:20px;
    margin-bottom:10px;
`
export const AboutDesc = styled.div`
    font-size:14px;
    color:#503e2c;
    p{
        line-height:1.5;
    }
`


