import styled from 'styled-components';

export const UserWrapper = styled.div`
    overflow:hidden;
    margin-top:20px;
    margin-bottom:20px;
    width:600px;
    min-height:600px;
`

export const TopMain = styled.div`
    padding: 10px;
    font-size: 14px;
    line-height: 120%;
    text-align: left;
    border-bottom: 1px solid #e2e2e2;
    background-color:#fff;
`

export const TopMainName = styled.div`
    display:inline-block;
    font-size: 24px;
    font-weight: 500;
    line-height: 150%;
    margin: 0px;
    padding: 0px;
    padding-right:10px;
`

export const TopSub = styled.div`
    font-size:14px;
    display:inline-block;
    color: #999;
`
export const MainWrapper = styled.div`
    padding: 0 10px;
    margin-bottom:20px;
    margin-top:10px;
    background-color:#fff;
`
export const MainTabs = styled.div`

    .active{
        border-bottom: 3px solid #778087;
    }
`
export const TabsItem = styled.div`
    display:inline-block;
    padding:10px 15px;
    font-size:14px;
    cursor:pointer;
`

export const MainList = styled.div`
    background-color:#fff;
    margin-top:10px;
`

export const Item = styled.div`
    position: relative;
    margin-bottom:20px;
    padding:10px;
    font-size: 14px;
    color:#503e2c;
    background:#fff;
    box-shadow: 0 2px 3px rgba(0,0,0,.1);
    .content{
        padding: 5px 0 10px;
        word-wrap: break-word;
        word-break: break-all;
        line-height:1.5;
        border-bottom: 1px solid #efefef;
    }
    .author{
        text-align:right;
        padding: 10px 0 5px;
        color:#999;
        font-size: smaller;
        .name{
            display:inline-block;
            padding-right:5px;
        }
        .time{
            display:inline-block;
        }
    }
`

export const PageWrapper = styled.div`
    overflow:hidden;
    background-color:#fff;
    padding:0 10px;
    margin-bottom:20px;
    color:#777;
    font-size:14px;
    text-align:center;
`
export const PageLeft = styled.div`
    float:left;
    width:100px;
    padding:10px 0;
    cursor:pointer;
    :hover{
        color:#000;
    }
`
export const PageMiddle = styled.div`
    width:calc(100% - 200px);
    float:left;
    padding:10px 0;
`
export const PageRight = styled.div`
    float:right;
    width:100px;
    padding:10px 0;
    cursor:pointer;
    :hover{
        color:#000;
    }
`


