import React,{ Component } from 'react';
import {connect} from 'react-redux';
import * as actionCreators from './store/actionCreators';
import {Redirect } from 'react-router-dom';
import * as safe from '../../safe';
import * as pubFn from '../../public';
import {
    AddWrapper,
    AddLeft,
    AddLeftContent,
    Timer,
    TextInput,
    BottomWrapeer,
    PostBtn,
    SwitchBtn
} from './style';

class AddTree extends Component{
    constructor(props){
        super(props);

        this.state={
            nowTime:pubFn.showTime()
        };
    }

    componentWillUnmount(){
        const {resetData} = this.props;
        resetData();
    }


    render(){
        const {nowTime} = this.state;
        const {loginState,value,postData,postStatus,anonymous,changeAnonymous,pub,changePublic,changeContent} = this.props;
        if(loginState){
            if (postStatus) return <Redirect to='/' />
            return(
                <AddWrapper className='container'>
                    <AddLeft>
                        <AddLeftContent>
                            <Timer>{nowTime}</Timer>
                            <TextInput defaultValue={value} onChange={changeContent} maxLength="512"></TextInput>
                            <BottomWrapeer>
                            <SwitchBtn>
                                <span className="margin-r">公开</span>
                                <label className="el-switch el-switch-green">
                                    <input type="checkbox" onChange={()=>{changePublic(pub)}} checked={pub} name="public" />
                                    <span className="el-switch-style"></span>
                                </label>
                                {
                                    pub ? (<span>
                                        <span className="margin-r margin-l">匿名</span>
                                        <label className="el-switch el-switch-green">
                                            <input type="checkbox" onChange={()=>{changeAnonymous(anonymous)}} checked={anonymous} name="anonymous" />
                                            <span className="el-switch-style"></span>
                                        </label>
                                    </span>) :''
                                }
                                 
                            </SwitchBtn>
                                <PostBtn onClick={postData}>发布</PostBtn>
                            </BottomWrapeer>
                        </AddLeftContent>
                        
                    </AddLeft>
                </AddWrapper>
            )
        }else{
            return <Redirect to='/Login'/>
        }
        
    }

}


const mapState = (state)=>{
    return {
        loginState:state.getIn(['login','login']),
        anonymous:state.getIn(['addTree','anonymous']),
        pub:state.getIn(['addTree','pub']),
        postStatus:state.getIn(['addTree','postStatus']),
        value:state.getIn(['addTree','value'])
    }
}

const mapDispath = (dispatch)=>{
    return{
        changeAnonymous(anonymous){
            dispatch(actionCreators.changeAnonymous(!anonymous));
        },
        changePublic(pub){
            dispatch(actionCreators.changePublic(!pub));
        },
        changeContent(e){
            let value = e.currentTarget.value;
            value = safe.htmlEscape(value);
            dispatch(actionCreators.pushValue(value));
        },
        resetData(){
            dispatch(actionCreators.reset());
        },
        postData(){
            dispatch(actionCreators.postData());
        }
    }
}
export default connect(mapState,mapDispath)(AddTree);


