import React,{Component} from 'react';
import {
    ToNightWrapper,
    ToNightLeft,
    ToNightRight,
    AboutWrapper,
    AboutTitle,
    AboutDesc
} from './style';
import List from './compontn/List';
class ToNight extends Component {
    
    render(){
        return(
            <ToNightWrapper className='container'>
                <ToNightLeft>
                    <List />
                </ToNightLeft>
                <ToNightRight>
                    <AboutWrapper>
                        <AboutTitle>天黑以后</AboutTitle>
                        <AboutDesc>
                            <p>每天晚上10点以后管理员开通并推送当天的主题。</p>
                            <br/>
                            <p>本版块提供留言功能，严禁广告行为。</p>
                        </AboutDesc>
                    </AboutWrapper>
                </ToNightRight>
            </ToNightWrapper>
        )
    }

    componentDidMount(){
        let body = document.body;
        body.style.backgroundColor = 'rgba(0,0,0,.7)';
    }

    componentWillUnmount(){
        let body = document.body;
        body.style.backgroundColor = '#f5f6f7';
    }
}

export default ToNight;


