import React,{Component} from 'react';
import {connect} from 'react-redux';
import {actionCreators} from '../store';
import {
    ListWrapper,
    Item,
    PageWrapper,
    PageLeft,
    PageMiddle,
    PageRight
} from '../style';
class List extends Component{
    render(){
        const {list,pageNum,lastPage,nextPage,totalPage} = this.props;
        return(
            <ListWrapper>
                {   
                    list.size > 0 ? list.toJS().map((item)=>{
                        return (
                            <Item key={item.id}>
                                <div className='content'>
                                    {item.desc}
                                </div>
                                <div className='author'>
                                    <span className='name'>{item.name}</span>
                                    <span className='time'>{item.creat_time}</span>
                                </div>
                            </Item>
                        );
                    }) : <div className='noData'>暂无数据...</div>
                }
                {
                    list.size > 0 ? (<PageWrapper>
                                        {
                                            pageNum > 1 ?<PageLeft onClick={()=>{lastPage(pageNum-1)}}>上一页</PageLeft> :<PageLeft>&nbsp;&nbsp;&nbsp;</PageLeft>
                                        }
                                        <PageMiddle>第{pageNum}页/共{totalPage}页</PageMiddle>

                                        {
                                            pageNum >= totalPage ?  <PageRight>&nbsp;&nbsp;&nbsp;</PageRight>: <PageRight onClick={()=>{nextPage(pageNum+1)}}>下一页</PageRight>
                                        }
                                    </PageWrapper>) :''
                }
                
                
            </ListWrapper>
        )
    }

    

    componentDidMount(){
        const {getList,pageNum} = this.props;

        getList(pageNum);
    }
}

const mapState = (state)=>{
    return {
        list:state.getIn(['home','list']),
        pageNum:state.getIn(['home','pageNum']),
        totalPage:state.getIn(['home','totalPage'])
    }
}

const mapDispath = (dispatch)=>{
    return{
        getList(pageNum){
            dispatch(actionCreators.getList(pageNum));
        },
        lastPage(pageNum){
            console.log(pageNum);
            dispatch(actionCreators.getList(pageNum));
        },
        nextPage(pageNum){
            console.log(pageNum);
            dispatch(actionCreators.getList(pageNum));
        }
    }
}


export default connect(mapState,mapDispath)(List);


