import React, { Component } from 'react';
import {connect} from 'react-redux';
import * as actionCreators from '../store/actionCreators';
import {
    MainWrapper,
    MainTabs,
    TabsItem,
    MainList,
    Item,
    PageWrapper,
    PageLeft,
    PageMiddle,
    PageRight
} from '../style';


class MainWrapperDiv extends Component{
    constructor(props) {
        super(props);
        this.state = {
            tabs: [
                {
                    active: true,
                    title: '私密树洞',
                    type: 'private'
                },
                {
                    active: false,
                    title: '公开树洞',
                    type: 'public'
                }
            ],
            selectType:'private'
        };

    }
    
    //改变标签
    changeTabs(i) {
        const {getList,pageNum} = this.props;
        let newList = this.state.tabs.map((item, index) => {
            if (index === i) {
                item.active = true;
                getList(item.type,pageNum);
                this.setState({
                    selectType:item.type
                })
            } else {
                item.active = false;
            }
            return item;
        });
        this.setState({
            tabs: newList
        });
    };

    //渲染标签
    renderTab() {
        let Tabs = this.state.tabs.map((item, index) => {
            return (
                <TabsItem key={index} onClick={this.changeTabs.bind(this, index)} className={item.active ? 'active' : ''}>{item.title}</TabsItem>
            );
        });
        return Tabs;
    }


    componentDidMount(){
        const {getList,pageNum} = this.props;
        getList(this.state.selectType,pageNum);
    }

    render(){
        const {list,lastPage,nextPage,pageNum,totalPage} = this.props;
        
        return (
            <MainWrapper>
                <MainTabs>
                    {this.renderTab()}
                </MainTabs>
                <MainList>
                    {
                        list.toJS().map((item) => {
                            return (
                                <Item key={item.id}>
                                    <div className='content'>{item.desc}</div>
                                    <div className='author'>
                                        <span className='name'>创建时间:</span>
                                        <span className='time'>{item.creat_time}</span>
                                    </div>
                                </Item>
                            );
                        })
                    }
                </MainList>

                {
                    list.size > 0 ? (<PageWrapper>
                                        {
                                            pageNum > 1 ?<PageLeft onClick={()=>{lastPage(this.state.selectType,pageNum-1)}}>上一页</PageLeft> :<PageLeft>&nbsp;&nbsp;&nbsp;</PageLeft>
                                        }
                                        <PageMiddle>第{pageNum}页/共{totalPage}页</PageMiddle>

                                        {
                                            pageNum >= totalPage ?  <PageRight>&nbsp;&nbsp;&nbsp;</PageRight>: <PageRight onClick={()=>{nextPage(this.state.selectType,pageNum+1)}}>下一页</PageRight>
                                        }
                                    </PageWrapper>) :''
                }
            </MainWrapper>
        )
    }
}

const mapState = (state)=>{
    return{
        list:state.getIn(['user','list']),
        pageNum:state.getIn(['user','pageNum']),
        totalPage:state.getIn(['user','totalPage'])
    }
}

const mapDispath = (dispatch)=>{
    return{
        getList(type,pageNum){
            dispatch(actionCreators.getList(type,pageNum));
        },
        lastPage(type,pageNum){
            dispatch(actionCreators.getList(type,pageNum));
        },
        nextPage(type,pageNum){
            dispatch(actionCreators.getList(type,pageNum));
        }
    }
}
export default connect(mapState,mapDispath)(MainWrapperDiv);


