import * as actionType from './actionType';
import axios from 'axios';
import Cookies from 'js-cookie';

const changeLogin = (r)=>{
    return{
        type:actionType.LOGIN,
        login:r.login,
        name:r.name,
        session:r.session,
        email:r.email,
        creat_time:r.creat_time
    }
}

const checkLoginFn = (r)=>{
    return{
        type:actionType.LOGIN_CHECK,
        login:r.login,
        name:r.name,
        session:r.session,
        email:r.email,
        creat_time:r.creat_time
    }
}   

export const login = (account,pass)=>{
    return (dispatch) => {
        axios.get('/api/login.json?account='+account+'&pass='+pass).then((res)=>{
            const r = res.data.data;
            if(r){
                Cookies.set('login',r.login,{ expires: 2 });
                Cookies.set('name',r.name,{ expires: 2 });
                Cookies.set('session',r.session,{ expires: 2 });
                Cookies.set('email',r.email,{expires:2});
                Cookies.set('creat_time',r.creat_time,{expires:2});
                dispatch(changeLogin(r));
            }else{
                console.log('登录失败!');
                // dispatch(changeLogin(r));
            }
        })
    }
}

export const checkLogin = ()=>{
    return (dispatch) =>{

        let login = Cookies.get('login');
        let name = Cookies.get('name');
        let session = Cookies.get('session');
        let email = Cookies.get('email');
        let creat_time = Cookies.get('creat_time');
        let r = {
            "login":login,
            "name":name,
            "session":session,
            "email":email,
            "creat_time":creat_time
        }
        
        dispatch(checkLoginFn(r));
    }
}

export const loginOut = ()=>{
    Cookies.remove('name');
    Cookies.remove('login');
    Cookies.remove('session');
    Cookies.remove('email');
    Cookies.remove('creat_time');
    return{
        type:actionType.LOGIN_OUT
    }
}


