import styled from 'styled-components';

export const AddWrapper = styled.div`
    overflow:hidden;
    margin-top:20px;
    margin-bottom:20px;
`

export const AddLeft = styled.div`
    float:left;
    width:100%;
    background:#fff;
    min-height:600px;
`

export const AddLeftContent = styled.div`
    overflow:hidden;
    padding:10px;
`

export const Timer = styled.div`
    font-size: 15px;
    padding-bottom:10px;
`
export const TextInput = styled.textarea`
    overflow:hidden;
    height:200px;
    width:calc(100% - 20px);
    resize:none;
    font-size:15px;
    padding:10px;
    margin-bottom:20px;
`
export const BottomWrapeer = styled.div`
    overflow:hidden;
`
export const PostBtn = styled.div`
    float:right;
    padding:10px 30px;
    border: 1px solid #34495E;
    font-size: 15px;
    color: #34495E;
    cursor: pointer;
    :hover{
        background: #34495E;
        color: #fff;
    }
`

export const SwitchBtn = styled.div`
    display:inline-block;
`


