import * as actionType from './actionType';

const postDataFn = ()=>{
    return{
        type:actionType.POST_DATA
    }
}


export const changeAnonymous = (anonymous)=>{
    return {
        type:actionType.CHANGE_ANONYMOUS,
        anonymous:anonymous
    }
}

export const changePublic = (pub)=>{
    return{
        type:actionType.CHANGE_PUBLIC,
        pub:pub
    }
}

export const pushValue = (value)=>{
    return {
        type:actionType.PUSH_VALUE,
        value:value
    }
}

export const postData = ()=>{
    return (dispatch)=>{
        dispatch(postDataFn());
    }
}

export const reset = ()=>{
    return{
        type:actionType.RESET_DATA
    }
}



