import {fromJS} from 'immutable';
import * as actionType from './actionType';

const defaultState = fromJS({
    value:'',
    pub:false,
    anonymous:false,
    postStatus:false
});

const resetData = (state)=>{
    return state.merge({
        value:'',
        pub:false,
        anonymous:false,
        postStatus:false
    })
}


export default (state=defaultState,action)=>{
    switch(action.type){
        case actionType.CHANGE_ANONYMOUS:
            return state.set("anonymous",action.anonymous);
        case actionType.CHANGE_PUBLIC:
            return state.set('pub',action.pub);
        case actionType.PUSH_VALUE:
            return state.set('value',action.value);
        case actionType.POST_DATA:
            return state.set('postStatus',true);
        case actionType.RESET_DATA:
            return resetData(state);
        default:
            return state;
    }
}


