import { fromJS } from 'immutable';
import * as actionType from './actionType';

const defaultState = fromJS({
   login:false,
   name:'',
   session:'',
   email:'',
   creat_time:''
});

//登录方法 
const login = (state,action)=>{
    return state.merge({
        "login":action.login,
        "name":action.name,
        "session":action.session,
        "email":action.email,
        "creat_time":action.creat_time
    });
}
const loginOut = (state,action)=>{
    
    return state.merge({
        "login":false,
        "name":'',
        "session":'',
        "email":'',
        "creat_time":''
    });
}


export default (state = defaultState,action)=>{
    switch(action.type){
        case actionType.LOGIN:
            return login(state,action);
        case actionType.LOGIN_CHECK:
            return login(state,action);
        case actionType.LOGIN_OUT:
            return loginOut(state,action);
        default:
            return state;
    }

}


