import React, { PureComponent } from 'react';
import {
    LoginWrapper,
    AdminLoginWrapper
} from '../Login/style';

class Reg extends PureComponent {
    render() {
        return (
            <LoginWrapper>
                <AdminLoginWrapper className='admin_login'>
                    <dt>
                        <strong>注册树说</strong>
                        <em>述说你的故事</em>
                    </dt>
                    <dd className="user_icon">
                        <input type="text" name="account" placeholder="登录账号" className="login_txtbx"/>
                    </dd>
                    <dd className="name_icon">
                        <input type="text" name="username" placeholder="用户名" className="login_txtbx" />
                    </dd>
                    <dd className="email_icon">
                        <input type="text" name="email" placeholder="邮箱" className="login_txtbx" />
                    </dd>
                    <dd className="pwd_icon">
                        <input type="password" name="password1" placeholder="密码" className="login_txtbx" />
                    </dd>
                    <dd className="pwd_icon">
                        <input type="password" name="password2" placeholder="确认密码" className="login_txtbx" />
                    </dd>
                    <dd className="val_icon">
                        <div className="checkcode">
                            <input type="text" id="J_codetext" placeholder="验证码" maxLength="4" className="login_txtbx" name="code" />
                            <img id="code_img" src="http://www.yuxi-paper.com/api.php?op=checkcode&amp;code_len=4&amp;font_size=20&amp;width=110&amp;height=50&amp;font_color=&amp;background=" />
                        </div>
                        <input type="button" value="换一个" className="ver_btn" />
                    </dd>
                    <dd>
                        <input type="submit" value="立即注册" className="submit_btn" />
                    </dd>
                    <dd>
                        <p>修改密码请联系管理员</p>
                        <p>管理员邮箱：byzhkc3@gmail.com</p>
                    </dd>
                </AdminLoginWrapper>
            </LoginWrapper>
        )
    }
}

export default Reg;


