import styled from 'styled-components';

export const HomeWrapper = styled.div`
    overflow:hidden;
    margin-top:20px;
`

export const HomeLeft = styled.div`
    float:left;
    width:570px;
`
export const ListWrapper = styled.div`

`

export const Item = styled.div`
    position: relative;
    margin-bottom:15px;
    padding:10px;
    font-size: 14px;
    color:#503e2c;
    background:#fff;
    box-shadow: 0 2px 3px rgba(0,0,0,.1);
    .content{
        padding: 5px 0 10px;
        word-wrap: break-word;
        word-break: break-all;
        line-height:1.5;
        border-bottom: 1px solid #efefef;
    }
    .author{
        text-align:right;
        padding: 10px 0 5px;
        color:#999;
        font-size: smaller;
        .name{
            display:inline-block;
            padding-right:10px;
        }
        .time{
            display:inline-block;
        }
    }
`
export const PageWrapper = styled.div`
    overflow:hidden;
    background-color:#fff;
    padding:0 10px;
    margin-bottom:20px;
    color:#777;
    font-size:14px;
    text-align:center;
`
export const PageLeft = styled.div`
    float:left;
    width:100px;
    padding:10px 0;
    cursor:pointer;
    :hover{
        color:#000;
    }
`
export const PageMiddle = styled.div`
    width:calc(100% - 200px);
    float:left;
    padding:10px 0;
`
export const PageRight = styled.div`
    float:right;
    width:100px;
    padding:10px 0;
    cursor:pointer;
    :hover{
        color:#000;
    }
`
export const HomeRight = styled.div`
    float:right;
    width:260px;
`

export const PostBtnWrapper = styled.div`
    margin-bottom: 20px;
    background: #fff;
    padding: 20px;
`
export const PostBtn = styled.div`
    height: 58px;
    text-align: center;
    line-height: 58px;
    width: 220px;
    border: 1px solid #34495E;
    font-size: 20px;
    color: #34495E;
    cursor:pointer;
    :hover{
        background: #34495E;
        color: #fff;
    }
`
export const AboutWrapper = styled.div`
    margin-bottom: 20px;
    background: #fff;
    padding: 20px;
`

export const AboutTitle = styled.div`
    text-align:center;
    font-size:20px;
    margin-bottom:10px;
`

export const AboutDesc = styled.div`
    font-size:14px;
    color:#503e2c;
    p{
        line-height:1.5;
    }
`



