import * as actionType from './actionType';
import axios from 'axios';


const getListFn = (r,pageNum)=>{
    return{
        type:actionType.GET_USER_POST_LIST,
        list:r,
        pageNum:pageNum
    }
}

export const getList = (type,pageNum)=>{
    return (dispatch)=>{
        switch(type){
            case 'private':
                axios.get('/api/private.json').then((res)=>{
                    const r = res.data.data;
                    if(r){
                        dispatch(getListFn(r,pageNum));
                    }else{
                        console.log('获取失败!');
                        // dispatch(changeLogin(r));
                    }
                });
                break;
            default:
                axios.get('/api/public.json').then((res)=>{
                    const r = res.data.data;
                    if(r){
                        dispatch(getListFn(r,pageNum));
                    }else{
                        console.log('获取失败!');
                        // dispatch(changeLogin(r));
                    }
                });
        }
        
    }   
}


