import * as actionType from './actionType';
import axios from 'axios';

const getListFn = (r,pageNum)=>{
    return{
        type:actionType.GET_LIST,
        list:r,
        pageNum:pageNum
    }
}

export const getList = (pageNum)=>{
    return (dispatch) =>{
        axios.get('/api/list.json').then((res)=>{
            const r = res.data.data;
            if(r){
                dispatch(getListFn(r,pageNum));
            }else{
                console.log('登录失败!');
                // dispatch(changeLogin(r));
            }
        })
    }
}

export const lastPage = (pageNum)=>{
    return {
        type:actionType.LAST_PAGE,
        pageNum:pageNum
    }
}

export const nextPage = (pageNum)=>{
    return {
        type:actionType.NEXT_PAGE,
        pageNum:pageNum
    }
}




