import React,{Component} from 'react';
import {
    AboutWrapper,
    AboutTitle,
    AboutDesc
} from '../style';

class About extends Component {
    render(){
        return (
            <AboutWrapper>
                <AboutTitle>关于树说</AboutTitle>
                <AboutDesc>
                    <p>树说这个网站，缘起于我在生活中遇到的事；但这些事情或者小心情你却无法跟身边任何述说，然后就有了建立树说这个网站的想法。</p>
                    <br/>
                    <p>你可以在这里畅所欲言，发泄你内心那些不曾也无法跟身边的人说的事。当然，这些都是在禁止谩骂他人或攻击他人的基础上。</p>
                    <br/>
                    <p>我不建议大家在发表的内容里留下任何联系方式，树说的初衷在于让你无法述说的心情得以释放，而不是社交，最主要的是在网上也请大家多注意个人隐私的保护，所以请谨记！</p>
                    <br/>
                    <p>以上，请尽情树说。</p>
                    <br/>
                    <p>管理员：ByZHkc3</p>
                    <p>Email：byzhkc3@gmail.com</p>
                </AboutDesc>
            </AboutWrapper>
        )
    }
}

export default About;


