import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {
    HeaderWrapper,
    HeaderLeft,
    HeaderRight
} from './style';
import {actionCreators as loginActionCreators } from '../Login/store';


class Header extends Component{

    // 判断是否登录
    isLogin(){
        const {loginState,name,loginOut} = this.props;
        if(loginState){
            return(
                <span>
                    <Link to='/AddTree'>
                        <span className="item">树说</span>
                    </Link>
                    <Link to='/User'>
                        <span className="item">{name}</span>
                    </Link>
                    <Link to='/Setting'>
                        <span className="item">设置</span>
                    </Link>

                    <span className="item" onClick={loginOut}>退出</span>  
                </span>
            )     
        }else{
            return (
                <span>
                    <Link to='/Login'>
                        <span className="item">登录</span>
                    </Link>
                    <Link to='/Reg'>
                        <span className="item">注册</span>
                    </Link>
                </span>
               
            )
        }
    }


    render(){
        return(
            <HeaderWrapper>
                <div className="container">
                    <HeaderLeft>
                        <Link to={'/'}>
                            <span className='title'>树说</span>
                            <i className="subTitle">述说你的故事...</i>
                        </Link>
                    </HeaderLeft>
                    <HeaderRight>
                        <Link to={'/'}>
                            <span className="item">首页</span>
                        </Link>
                        {/* <Link to='/ToNight'>
                            <span className="item">天黑以后</span>
                        </Link> */}

                        { this.isLogin() }
                        
                    </HeaderRight>
                </div>
            </HeaderWrapper>
        )
    }


    componentDidMount(){
        this.props.checkLogin();
    }

}



const mapState = (state)=>{
    return {
        loginState:state.getIn(['login','login']),
        name:state.getIn(['login','name'])
    }
}

const mapDispath = (dispatch)=>{
    return{
        checkLogin(){
            dispatch(loginActionCreators.checkLogin());
        },
        loginOut(){
            dispatch(loginActionCreators.loginOut())
        }
    }
}


export default connect(mapState,mapDispath)(Header);


