import styled from 'styled-components';

export const HeaderWrapper = styled.div`
    height: 45px;
    background-color: #2c405a;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    .container{
        overflow:hidden;
        height:45px;
        line-height:45px;
        color:#fff;
    }
`

export const HeaderLeft = styled.div`
    float:left;
    width:30%;
    height:45px;
    line-height:45px;
    .title{
        font-size:24px;
        letter-spacing: 6px;
        padding-right:10px;
    }
    .subTitle{
        ${'' /* vertical-align: -webkit-baseline-middle; */}
        vertical-align:middle;
        color:#ccc;
    }
`
export const HeaderRight = styled.div`
    float:right;
    .item{
        display:inline-block;
        color: #ddd;
        font-size: 16px;
        line-height: 45px;
        padding:0 10px;
        cursor:pointer;
    }
    .item:hover{
        color:#fff;
    }
`



