import React, { Component } from 'react';
import {connect} from 'react-redux';
import {
    ItemWrapper,
    Item
} from '../style';


class ItemWrapperDiv extends Component{
    render (){
        const {name,email} = this.props;
        return(
            <ItemWrapper>
                <Item>用户名:{name}</Item>
                <Item>电子邮箱:{email}</Item>
            </ItemWrapper>
            
        )
    }
}


const mapState = (state) =>{
    return{
        "name":state.getIn(['login',"name"]),
        "email":state.getIn(['login','email'])
    }
}

export default connect(mapState,null)(ItemWrapperDiv);


