import React, { Component } from 'react';
import {connect} from 'react-redux';
import {
    TopMain,
    TopMainName,
    TopSub,
} from '../style';

class TopMainDiv extends Component{
    render(){
        const {name,creat_time} = this.props;
        return (
            <TopMain>
                <TopMainName>{name}</TopMainName>
                <TopSub>加入于 {creat_time}</TopSub>
            </TopMain>
        )
    }
}

const mapState = (state)=>{
    return{
        'name':state.getIn(['login','name']),
        'creat_time':state.getIn(['login','creat_time'])
    }
}

export default connect(mapState,null)(TopMainDiv);


