import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import {
    HomeWrapper,
    HomeLeft,
    HomeRight,
    PostBtnWrapper,
    PostBtn,
} from './style';
import List from './component/List';
import About from './component/About';

class Home extends Component{
    render(){
        return(
            <HomeWrapper className='container'>
                <HomeLeft>
                    <List/>
                </HomeLeft>
                <HomeRight>
                    <PostBtnWrapper>
                        <Link to={'/AddTree'}>
                            <PostBtn>述说我的故事</PostBtn>
                        </Link>
                    </PostBtnWrapper>
                    <About />
                </HomeRight>
            </HomeWrapper>
        )
    }
}

export default Home;


