import React, { PureComponent } from 'react';
import {connect} from 'react-redux';
import {actionCreators} from './store';
import {Redirect } from 'react-router-dom';
import * as safe from '../../safe';
import { 
    LoginWrapper,
    AdminLoginWrapper
} from './style';

class Login extends PureComponent {

    constructor(props){
        super(props)

        this.account = React.createRef();
        this.pass = React.createRef();
    }
    render() {
        const {login,loginState} = this.props;
       
        if(!loginState){
            return(
                <LoginWrapper>
                    <AdminLoginWrapper className='admin_login'>
                        <dt>
                            <strong>登录树说</strong>
                            <em>述说你的故事</em>
                        </dt>
                        <dd className="user_icon">
                            <input type="text"  name="username" placeholder="账号" className="login_txtbx" ref={this.account} />
                        </dd>
                        <dd className="pwd_icon">
                            <input type="password" name="password" placeholder="密码" className="login_txtbx" ref={this.pass} />
                        </dd>
                        <dd className="val_icon">
                            <div className="checkcode">
                                <input type="text" id="J_codetext" placeholder="验证码" maxLength="4" className="login_txtbx" name="code" />
                                <img id="code_img" src="http://www.yuxi-paper.com/api.php?op=checkcode&amp;code_len=4&amp;font_size=20&amp;width=110&amp;height=50&amp;font_color=&amp;background=" />
                            </div>
                            <input type="button" value="换一个" className="ver_btn" />
                        </dd>
                        <dd>
                            <input type="submit" value="立即登陆" className="submit_btn"  onClick={()=>{login(this.account.current,this.pass.current)}}/>
                        </dd>
                        <dd>
                            <p>修改密码请联系管理员</p>
                            <p>管理员邮箱：byzhkc3@gmail.com</p>
                        </dd>
                    </AdminLoginWrapper>
                </LoginWrapper>
            )
        }else{
            return(
                <Redirect to="/" />
            )
        }
         

    }


    
}

const mapState = (state)=>{
    return {
        loginState:state.getIn(['login','login'])
    }
}

const mapDispatch = (dispatch)=>{
    return{
        login(accountEle,passEle){
            if(accountEle.value === '' || passEle.value === '' ){
                alert('用户名或密码不能为空!');
                return ;
            }
            dispatch(actionCreators.login(safe.htmlEscape(accountEle.value),safe.htmlEscape(passEle.value)));
        }
    }
}
export default connect(mapState,mapDispatch)(Login);


