import React, { Component } from 'react';
import {BrowserRouter,Route,Switch} from 'react-router-dom';
import { Provider } from 'react-redux'; 
import store from './store/index';


import Header from '../src/common/Header';
import Footer from '../src/common/Footer';
import Home from '../src/pages/Home';
import AddTree from '../src/pages/AddTree';
import ToNight from '../src/pages/ToNight';
import User from '../src/pages/User';
import Setting from '../src/pages/Setting';
import Login from '../src/common/Login'
import Reg from '../src/common/Reg';



class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <BrowserRouter>
          
          <div>
            <Header/>
            <Switch>
              <Route path='/' exact component={Home}></Route>
              <Route path='/AddTree' exact component={AddTree}></Route>
              <Route path="/ToNight" exact component={ToNight}></Route>
              <Route path='/User' exact component={User}></Route>
              <Route path='/Setting' exact component={Setting}></Route>
              <Route path='/Login' exact component={Login}></Route>
              <Route path='/Reg' exact component={Reg}></Route>
              <Route component={Home}></Route>
            </Switch>
            <Footer/>
          </div>
        </BrowserRouter>
      </Provider>
     
    );
  }
}

export default App;



